#experience {
  background-color: var(--section-bg-color-primary);
  color: var(--section-color-primary);
  flex-direction: column;
  gap: 1rem;
  display: flex;
}

#experience h1 {
  text-align: center;
  text-transform: uppercase;
  margin: 0 20vw;
  padding: .5rem;
}

#experience-container {
  flex-direction: row;
  display: flex;
}

#experience-container .slide-arrow-container {
  height: 100%;
  width: 10%;
  place-items: center;
  display: grid;
}

#experience-container .slide-arrow-container:hover {
  cursor: pointer;
}

#experience-container .slide-arrow {
  width: 1rem;
  height: 1rem;
}

#experience-list {
  width: 80%;
  padding: 1rem 0;
}

#experience-list h3 {
  text-align: center;
}

#experience .duration {
  text-align: center;
  width: 100%;
  color: gray;
  font-size: .7rem;
  font-style: italic;
  display: inline-block;
}

#experience .responsibilities {
  padding-top: 1rem;
  font-size: .8rem;
}

#experience .responsibilities li {
  margin-bottom: 1rem;
}

@media only screen and (min-width: 768px) {
  #experience-list h3, #experience-list .duration {
    text-align: left;
  }

  #experience .duration, #experience .responsibilities {
    font-size: 1rem;
  }
}

